















import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'

@Component({
  components: {
    HelloWorld
  }
})
export default class Home extends Vue {
  loginClick = true
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { name: '', password: '' }
  fields =[{ type: 'input', modelKey: 'name', label: '账号', props: { placeholder: '请输入' }, rules: { required: true } }, { type: 'input', modelKey: 'password', label: '密码', props: { placeholder: '请输入', type: 'password' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  mounted () {
    console.log('钩子')
    // wx.config({
    //   debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
    //   appId: 'wxc6c897a704d3fac4', // 必填，公众号的唯一标识
    //   timestamp: , // 必填，生成签名的时间戳
    //   nonceStr: '', // 必填，生成签名的随机串
    //   signature: '',// 必填，签名
    //   jsApiList: [] // 必填，需要使用的JS接口列表
    // });
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  showAlert () {
    this['$createDialog']({
      type: 'alert',
      title: '我是标题',
      content: '我是内容',
      icon: 'cubeic-alert'
    }).show()
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    e.preventDefault()
    let pass = this.model.password
    let user = this.model.name
    if (!this.loginClick) {
      return
    }
    // if (!pass) {
    //   this['$createDialog']({
    //     type: 'alert',
    //     content: '请输入密码',
    //     icon: 'cubeic-wrong'
    //   }).show()
    //   return
    // }
    // if (!user) {
    //   this['$createDialog']({
    //     type: 'alert',
    //     content: '请输入账号',
    //     icon: 'cubeic-wrong'
    //   }).show()
    //   return
    // }
    const toast = this['$createToast']({
      time: 0,
      txt: '登陆中...'
    })
    toast.show()
    this.loginClick = false
    this.$store.dispatch('login', { user: user, pass: pass }).then((response) => {
      toast.hide()
      this.loginClick = true
      if (!response.success) {
        this['$createDialog']({
          type: 'alert',
          content: '账号或密码错误',
          icon: 'cubeic-wrong'
        }).show()
      }
    })
  }
  submitClick () {
    return false
  }
}
